<template>
<div class="report2" v-if="!!forms">
  <header>
    <h2>第二部分:温度调节</h2>
  </header>
  <section class="report_section">
    <div class="report_content" v-for="(form,key) in forms" :key="key">
      <div v-if="key == 'question1'">
        <h4>请问您所在办公室的种类？</h4>
        <div :ref="key" class="charts question1"></div>
      </div>
      <div v-if="key == 'question3'">
        <h4>在刚刚过去的供冷季里（6-8月），您在室内若感觉偏冷，会采取哪些措施缓解？（多选）</h4>
        <div :ref="key" class="charts question3"></div>
      </div>
      <div v-if="key == 'question4'">
        <h4>在刚刚过去的供冷季里（6-8月），您在室内若感觉偏热，会采取哪些措施缓解？（多选）</h4>
        <div :ref="key" class="charts question4"></div>
      </div>
      <div v-if="key == 'question5'">
        <h4>在刚刚过去的供冷季里，您是否经常使用朵朵APP调节办公室空调</h4>
        <div :ref="key" class="charts question5"></div>
      </div>
      <div v-if="key == 'question6'">
        <h4>在刚刚过去的供冷季里，你为什么频繁使用朵朵APP调节空调？</h4>
        <div :ref="key" class="charts question6"></div>
      </div>
      <div v-if="key == 'question7'">
        <h4>在刚刚过去的供冷季里，你为什么很少使用朵朵APP调节空调？</h4>
        <div :ref="key" class="charts question7"></div>
      </div>
      <div v-if="key== 'question8'||key== 'question9'||key== 'question10'">
        <h4 v-if="key== 'question8'">假设你在采用控制面板调节空调的单人办公室中工作，你在供冷季会如何使用办公室空调？</h4>
        <div v-if="key == 'question8'">
          <h4>开启方式（可多选）</h4>
          <div :ref="key" class="charts question8"></div>
        </div>
        <div v-if="key == 'question9'">
          <h4>关闭方式（可多选）</h4>
          <div :ref="key" class="charts question9"></div>
        </div>
        <div v-if="key == 'question10'">
          <h4>温度设定方式 （单选）</h4>
          <div :ref="key" class="charts question10"></div>
        </div>
      </div>
      <div v-if="key== 'question11'||key== 'question12'||key== 'question13'">
        <h4 v-if="key== 'question11'">如果在采用空调面板调节的多人办公室中，你会如何调节空调？</h4>
        <div v-if="key == 'question11'">
          <h4>开启方式（可多选）</h4>
          <div :ref="key" class="charts question11"></div>
        </div>
        <div v-if="key == 'question12'">
          <h4>关闭方式（可多选）</h4>
          <div :ref="key" class="charts question12"></div>
        </div>
        <div v-if="key == 'question13'">
          <h4>温度设定方式 （单选）</h4>
          <div :ref="key" class="charts question13"></div>
        </div>
      </div>
    </div>
  </section>
  <footer class="nextpage" @click="ShareMoments">
    分享朋友圈
  </footer>
</div>
</template>

<script>
import allForms from './questions'
import {
  onMenuShareTimeline,
  closeWindow
} from 'api/global'
import {
  getSecQuestionnaireAnswer
} from 'api/question'
import tools from 'utils/tools'
import * as echarts from 'echarts/lib/echarts'; //引入主模块
import 'echarts/lib/chart/line';
import 'echarts/lib/chart/bar';
import {
  Indicator,
  Toast
} from 'mint-ui';
export default {
  name: 'questionreport2',
  data: function() {
    return {
      forms: null
    }
  },
  created() {
    Indicator.open('加载中...');
  },
  mounted() {;
    this.getRenderData()
  },
  methods: {
    async ShareMoments() {
      //分享朋友圈
      let url = process.env.BASE_URL + '/questionsurvey'
      await onMenuShareTimeline({
        title: '环境问卷调查', // 分享标题
        link: url, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
        imgUrl: '../../../assets/question/question-share', // 分享图标
      })
    },
    drawChart() {
      this.$nextTick(() => {
        let forms = this.forms
        for (let question in forms) {
          if (question == 'question2') {
            //这个是时间的部分
          } else if (question == 'question1' || question == 'question5' || question == 'question6' || question == 'question7' || question == 'question10' || question == 'question13') {
            //单选题
            //y轴上的文字信息 
            let yText = forms[question].map((item, index) => {
              let name = item[index + 1] || '其他'
              if (name.length > 10) {
                name = name.slice(0, 10) + '\n' + name.substr(10)
              }
              return name
            }).reverse();
            let xText = forms[question].map((item, index) => {
              return {
                name: name = item[index + 1] || '其他',
                value: item.prcent,
                select: item.select
              }
            }).reverse();

            this.drawBar(this.$refs[question][0], yText, xText)
          } else {
            //多选题
            let yText = forms[question].map((item, index) => {
              let name = item[index + 1] || '其他'
              if (name.length > 10) {
                name = name.slice(0, 10) + '\n' + name.substr(10)
              }
              return name
            }).reverse();
            let xText = forms[question].map((item, index) => {
              return {
                name: name = item[index + 1] || '其他',
                value: item.prcent,
                select: item.select
              }
            }).reverse();

            this.drawBar(this.$refs[question][0], yText, xText)
          }
        }

      })
    },
    async getRenderData() {
      // let renderdata = await getSecQuestionnaireAnswer()
      // if (!!renderdata) {
      //   console.log(renderdata)
      //   let newObj = Object.entries(allForms).map((item, index) => {
      //     if (Array.isArray(item[1])) {
      //       //如果是数组
      //       return item[1].map((question, key) => {
      //         return Object.assign(question, renderdata[item[0]][key])
      //       })
      //     } else {
      //       //如果不是数组
      //       return null
      //     }
      //   })
      // }

      let renderdata = await this.mock()
      Indicator.close();
      this.forms = renderdata
      this.drawChart();
      // if (renderdata.data.Result == 'success') {

      // } else {
      //   Toast(renderdata.data.message.substr(0, 12))
      //   closeWindow()
      // }
    },
    mock() {
      return new Promise((resolve, reject) => {
        //第二部分的
        var add0 = num => {
          return num < 10 ? '0' + num : '' + num
        }
        var rP = arr => {
          var max = Math.max.apply(this, arr);
          var min = Math.min.apply(this, arr);
          return Math.round(Math.random() * (max - min) + min);
        }

        let times = []
        for (let i = 0; i < 12; i++) {
          times.push({
            hour: add0(i * 2) + 'h',
            value: rP([4, 20])
          })
        }



        var mockboolean = () => {
          return (parseInt(Math.random() * 10) > 5)
        }
        for (let question in allForms) {
          if (question == 'question2') {
            //这个是时间的部分


          } else if (question == 'quesiton1' || question == 'quesiton5' || question == 'quesiton6' || question == 'quesiton7' || question == 'quesiton10' || question == 'quesiton13') {
            //单选题
            allForms[question].forEach(obj => {
              obj.select = mockboolean()
              obj.prcent = parseInt(Math.random() * 100)
            })
          } else {
            //多选题
            allForms[question].forEach(obj => {
              obj.select = mockboolean()
              obj.prcent = parseInt(Math.random() * 100)
            })
          }
        }
        setTimeout(() => {
          resolve(allForms)
        }, 200);
      })
    },
    drawBar(dom, yText, xText) {
      dom.style.height = yText.length * 40 + 'px'
      let myChart = echarts.init(dom)
      myChart.setOption({
        "grid": {
          left: '40%',
          top: 0,
          right: '12%',
          bottom: 0
        },
        "yAxis": [{
          "type": "category",
          "data": yText,
          "axisLine": {
            "show": false
          },
          "axisTick": {
            "show": false,
            "alignWithLabel": true
          },
          "axisLabel": {
            align: 'right',
            // "textStyle": {
            //   "color": "#6D6D6D",
            //   align: 'right',
            // }
          },
          nameTextStyle: {
            align: 'right'
          },
          "nameLocation": 'start',
          "boundaryGap": ['20%', '20%']
        }],
        "xAxis": [{
          "type": "value",
          "axisLine": {
            "show": false
          },
          "axisTick": {
            "show": false
          },
          "axisLabel": {
            "show": false
          },
          "splitLine": {
            "show": false
          }
        }],
        "series": [{
          "name": "应用使用率",
          "type": "bar",
          "data": xText,
          "barCategoryGap": "35%",
          barWidth: 15,
          "label": {
            "normal": {
              "show": true,
              "position": "right",
              "formatter": function(params) {
                return params.data.value + '%';
              },
              "textStyle": {
                "color": "#6D6D6D" //color of value
              }
            }
          },
          "itemStyle": {
            "normal": {
              "color": function(params) {
                return params.data.select ? '#007AFF' : '#D8D8D8'
              }
            }
          }
        }]
      })
    },
  },


}
</script>
<style lang="less">
@import '../../../styles/mixin';
body {
  background-color: #fff;
}

.mint-indicator-wrapper {
  z-index: 2002;
}

@fs: 27/@rem;
.report2 {
  overflow: hidden;
  background-color: #fff;
  header {
    position: fixed;
    background: #fff;
    top: 0;
    left: 0;
    z-index: 1;
    #wh(100%, 111/@rem);
    padding: 0 40/@rem;
    h2 {
      height: 111/@rem;
      border-bottom: 1px solid #cacaca;
      font-size: 36/@rem;
      line-height: 119/@rem;
    }
  }
  .report_section {
    margin: 111/@rem 0;
    .report_content {
      h4 {
        padding: 12/@rem 40/@rem 0;
        #sc(30/@rem, #333);
        font-weight: normal;
      }
      .charts {
        width: 100%;
      }
    }
  }
}

.nextpage {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 2001;
  #wh(100%, 100/@rem);
  text-align: center;
  #sc(@fs, #fff);
  line-height: 100/@rem;
  background-image: linear-gradient(-174deg, #04A2FC 0%, #401CD5 100%);
}
</style>


